import { createHTMLElement } from '../../util';
const className = 'map-container';

function createMap() {
    // == PROPERTIES ==

    var /**
         * this object holds the properties and methods we want to return later
         * @var {Object}
         */
        returnObj = {},
        /**
         * data source - relative to handle production and test environments
         * @var {String}
         */
        dataSrc =
            'https://web2020.city.ac.uk/staging/development/walter/campus-map/locations',
        /**
         * Create a LatLng object containing the coordinate for the center of the map
         * @var {Object}
         */
        latlng = new google.maps.LatLng(51.527761, -0.103283),
        /**
         * jQuery object for map canvas
         * @var {Object}
         */
        $mapContainer = document.getElementById('map-container'),
        /**
         * an object literal containing the properties we want to pass to the map
         * @var {Object}
         */
        mapOptions = {
            zoom: 17,
            key: 'AIzaSyBvg6r1x2ZRKPAsceVaKPlg6tO20QiBDpo',
            center: latlng,
            mapTypeId: google.maps.MapTypeId.ROADMAP,
            streetViewControl: true,
            mapTypeControl: true,
            mapTypeControlOptions: {
                style: google.maps.MapTypeControlStyle.DROPDOWN_MENU,
            },
            navigationControl: true,
        },
        /**
         * Call the constructor, thereby initializing the map  as soon as possible
         * @var {Object}: Google maps Map object
         */
        map = new google.maps.Map(
            document.getElementById('map-canvas'),
            mapOptions
        ),
        searchBox = document.getElementById('map-search'),
        /**
         * initlaise infoWindow
         * @var {Object}: Google maps InfoWindow object
         */
        infoWindow = new google.maps.InfoWindow({
            maxWidth: 400,
        }),
        // walter >> this was commented out already
        /* points = [
        new google.maps.LatLng(51.527701, -0.102509),
        new google.maps.LatLng(51.521870, -0.090356)
        ];

        for (var i = 0; i < points.length; i++) {
        marker = new google.maps.Marker({
        position: points[i],
        animation: google.maps.Animation.DROP

        });
        }

        marker.setMap(map);
        map.panTo(marker.position);
        /**
         * cityLayers object literal containing skeleton options for City related things - parsing the xml file adds to this
         * it is where we keep arrays of markers to use when clearing/setting things on the map
         * @var {Object}:
         */
        cityLayers = {
            // array to hold each google marker object - for each University location
            bigMarkersArray: [],
            //arrray to hold building google marker objects - they never got removed from map
            bigBuildingsArray: [],

            buildingsObj: {},

            buildings: {
                toggler: null,
                markersArray: [],
                zoomLevel: 17,
                list: '',
            },
            libraries: {
                toggler: document.getElementById('toggle-libraries'),
                markersArray: [],
                zoomLevel: 17,
                list: '',
            },
            accommodation: {
                toggler: null,
                markersArray: [],
                zoomLevel: 17,
                list: '',
            },
            lectureTheatres: {
                toggler: null,
                markersArray: [],
                zoomLevel: 17,
                list: '',
            },
            studentCentre: {
                toggler: document.getElementById('toggle-studentCentre'),
                markersArray: [],
                zoomLevel: 17,
                list: '',
            },
            sports: {
                toggler: null,
                markersArray: [],
                zoomLevel: 17,
                list: '',
            },
            computerLabs: {
                toggler: document.getElementById('toggle-computerLabs'),
                markersArray: [],
                zoomLevel: 17,
                list: '',
            },
        },
        /**
         * kmlLayers object - kml files containing useful third party location information
         * @var {Object}:
         */
        kmlLayers = {
            cycleHire: {
                src: 'http://webapps.city.ac.uk/matrix/maps/cycle-hire.php',
                //toggler: document.getElementById('cycle-hire'),
                toggler: (function() {
                    var myvariable = document.getElementById('cycle-hire');
                    return myvariable;
                })(),
                preserveViewport: true,
            },
            bikes: {
                src: 'http://webapps.city.ac.uk/matrix/maps/cycle-parking.kml',
                toggler: document.getElementById('bikes'),
                preserveViewport: true,
            },
            atms: {
                src: 'http://webapps.city.ac.uk/matrix/maps/atms.kml',
                toggler: document.getElementById('cash-points'),
                preserveViewport: true,
            },
        },
        // == METHODS ==

        /**
         * Removes the overlays from the map, but keeps them in the array
         * @param {Array} markersArray: a collections of markers
         * @return {Undefined}
         */
        clearOverlays = function(markersArray) {
            
            var i;

            if (markersArray) {
                for (i in markersArray) {
                    if (
                        markersArray.hasOwnProperty(i) &&
                        typeof markersArray[i] !== 'function'
                    ) {
                        markersArray[i].setMap(null);
                    }
                }
            }
        },
        /**
         * the hashChange handler - listens for hash changes and then does cool stuff
         * @param {Object} e: the window hashchange event
         * @return {Undefined}
         */
        hashChange = function(e) {

            var state = e,
                x,
                i,
                el,
                ii,
                ee,
                marker;


    
            // walter >> review below
            //no active marker - means an info window was closed
            if (el === '0') {
                infoWindow.close();
                return;
            }

            //loop over  bigBuildingsArray to find marker
            //cityLayers.buildingsObj.forEach(function(ee, ii) {
            for (var ee in cityLayers.buildingsObj) {
                if (e === ee) {
                    var found = cityLayers.buildingsObj[ee];

                    marker = found;
                    //open infoWindow for this marker
                    //clear all overlays first
                    clearOverlays(cityLayers.bigMarkersArray);
                    //make marker visible
                    marker.setVisible(true);
                    //set infoWindow content
                    infoWindow.setContent(marker.infoHtml);
                    //open the infoWindow centered on the marker
                    infoWindow.open(map, marker);
                    //pan map to marker position
                    map.panTo(marker.position);
                    //add marker to map
                    marker.setMap(map);
                }
            }
        },
        /**
         * the updateHash called when a marker, infoWindow or marker link is clicked - updates hash
         * @param {Object || String} caller: the marker, marker link or infoWindow firing the event
         * @return {Undefined}
         */
        updateHash = function(caller) {
            var state = {},
                callerId = '';

            if (!caller) {
                return;
            }

            if (typeof caller === 'string') {
                //a marker link was clicked
                callerId = caller;
               // state[callerId] = 1;
            } else if (caller.hasOwnProperty('id')) {
                //a marker was clicked
                callerId = caller.id;
                //state[callerId] = 1;
            } else {
                //an infoWindow was closed
                callerId = 0;
            }

            // walter add comment
            location.hash = callerId;
            hashChange(callerId);
            
        },
        /**
         * Shows any overlays currently in the array
         * @param {Array} markersArray: a collection of markers
         * @return {Undefined}
         */
        showOverlays = function(markersArray) {
            var i;

            if (markersArray) {
                for (i in markersArray) {
                    if (
                        markersArray.hasOwnProperty(i) &&
                        typeof markersArray[i] !== 'function'
                    ) {
                        markersArray[i].setMap(map);
                    }
                }
            }
        },
        /**
         * creates a new category of place in CityLayers object
         * @param {String} name: the new name of the category ?
         * @return {Undefined}
         */
        newCategory = function(name) {
            name.toggler = null; // walter replaced with name - was this
            name.markersArray = [];
            name.zoomLevel = 17;
        },
        /**
         *  loop through each kmllayer adding checked behaviour
         * @param {Object} object: Config object for KML
         * @return {?}
         */
        initKmlLayers = function(layer) {
            for (var key in layer) {
                // skip loop if the property is from prototype
                if (!layer.hasOwnProperty(key)) {
                } //end if

                var obj = layer[key];

                var toggler = obj.toggler;
                var overlay = new google.maps.KmlLayer(obj.src, {
                    preserveViewport: obj.preserveViewport,
                });

                /* walter >>
                disabled for now as tonggler empty - maybe even remove klmlyer as possible not required


                toggler.addEventListener("click", function(){
                    console.log(`toggler`);
                    //toggler.toggleClass('enabled');
                    overlay.setMap(toggler.classList.contains('enabled') ? map : null);
                    return false;
                }); //end click fn */
            } // end for
        }, //end initKmlLayers
        /**
         *  creates individual markers, builds a corresponding filter item, binds a infoWindow to marker with html
         * @param {Object} markerConfig: an object containing various marker configuration options
         * @return {Object} marker - google maps marker
         */
        createMarker = function(markerConfig) {
            // var listId = '#' + markerConfig.category, >> walter maybe delete this now
            var listId = document.getElementById(markerConfig.category),
                listItem,
                marker,
                $li,
                $a,
                markerId,
                prefixHtml,
                html = '';

            //build html for infoWindow

            //if has link
            if (markerConfig.linkHref.length !== 0) {
                html =
                    '<div id="info-window" style="min-height: 100px;"><h3><a href="' +
                    markerConfig.linkHref +
                    '">' +
                    markerConfig.name +
                    '</a></h3>';
            } else {
                html =
                    '<div id="info-window" style="min-height: 100px;"><h3>' +
                    markerConfig.name +
                    '</h3>';
            }

            //test to see if has a buildingPrefix
            if (markerConfig.buildingPrefix.length !== 0) {
                html +=
                    '<p class="building-prefix"><strong>Rooms beginning: ' +
                    markerConfig.buildingPrefix +
                    '</strong></p>';
                markerConfig.buildingPrefix =
                    '(' + markerConfig.buildingPrefix + ')';
            }

            //add description and close div element
            html += markerConfig.description + '</div>';

            //create google maps marker
            marker = new google.maps.Marker({
                map: markerConfig.map,
                position: markerConfig.point,
                icon: markerConfig.icon,
                animation: google.maps.Animation.DROP,
            });

            //add a new property to the google maps marker object so we can id markers
            marker.set('id', markerConfig.id);
            //add a new property of inHtml to populate infoWindow
            marker.set('infoHtml', html);

            //attach event listener for marker click to handle infoWindow
            google.maps.event.addListener(marker, 'click', function(e) {
                //closure in a loop shizzle - I find this hard to get my head around - returning a closure seems to help
                return updateHash(marker);
            });

            //create list element

            $li = createHTMLElement('li', [
                { label: 'id', val: 'building-' + markerConfig.id },
                { label: 'class', val: 'building' },
            ]);

            //create a element with click handler to open infoWindow

            var $a = createHTMLElement('a', [
                {
                    label: 'html',
                    val:
                        '<span>' +
                        markerConfig.name +
                        ' ' +
                        markerConfig.buildingPrefix +
                        '</span>',
                },
                { label: 'href', val: '#' },
            ]);

            $a.addEventListener('click', function(e) {
                e.preventDefault();

                updateHash(
                    e.target.parentElement.parentElement
                        .getAttribute('id')
                        .replace('building-', '')
                );
                return false;
            });

            $a.style.background =
                'transparent url(' +
                markerConfig.icon +
                '?v=1123) no-repeat left center';

            //add li item to list

            listId ? listId.appendChild($li).appendChild($a) : null;
            return marker;
        }, //end fn.createMarker
        // Deletes all markers in the array by removing references to them
        //commented out to keep JSLint happy as this function is not currently used
        deleteOverlays = function(markersArray) {
            var i;

            if (markersArray) {
                for (i in markersArray) {
                    if (typeof markersArray[i] !== 'function') {
                        markersArray[i].setMap(null);
                    }
                }
                markersArray.length = 0;
            }
        },
        // >>>

        findChildText = function(node, name) {
            var value = '';

            var children = node.getElementsByTagName(name);
            var childrenLength = node.getElementsByTagName(name).length;

            if (childrenLength > 0) {
                value = children[0].textContent;
            }
            return value;
        },
        /**
         * parses locations xml and creates markers
         * @param {Object} xml: xml returned from Ajax request
         * @param {String} textStatus: standard ajax reposnse
         * @param {Object} textStatus: standard ajax reposnse
         * @return {?}
         */
        parseXml = function(xml, textStatus, jqXHR) {
            var items = xml.getElementsByTagName('item');

            var index = 0,
                $self,
                marker,
                markerConfig = {},
                searchTags = [],
                searchIds = {};

            items.forEach(function(item) {
                $self = item;

                markerConfig.index = index + 1;
                markerConfig.name = $self.getElementsByTagName('title')[0].textContent;
                markerConfig.linkHref = $self.getElementsByTagName('link')[0].textContent;
                markerConfig.description = $self.getElementsByTagName('description')[0].textContent;
                markerConfig.icon = findChildText($self, 'CUL:icon');
                markerConfig.category = $self.getElementsByTagName('category')[0].textContent;
                markerConfig.id = $self.getElementsByTagName('guid')[0].textContent;
                //markerConfig.isPolygon = $self.find("[nodeName='georss:polygon']"); always been commented out
                markerConfig.buildingPrefix = findChildText($self,'CUL:buildingPrefix');
                markerConfig.hexColour = findChildText($self, 'CUL:hexColour');
                markerConfig.geoLat = findChildText($self, 'geo:lat');
                markerConfig.geoLong = findChildText($self, 'geo:long');
    
                markerConfig.point = new google.maps.LatLng(
                    parseFloat(markerConfig.geoLat),
                    parseFloat(markerConfig.geoLong)
                );

                //call createmarker fn
                marker = createMarker(markerConfig);

                if (markerConfig.category !== 'buildings') {
                    //add returned marker to big array
                    cityLayers.bigMarkersArray.push(marker);
                } else {
                    //add marker to buldings array
                    cityLayers.bigBuildingsArray.push(marker);
                }
                //all markers go in here
                cityLayers.buildingsObj[marker.id] = marker;
        
                searchTags.push(createHTMLElement('a', [
                    {
                        label: 'content',
                        val: markerConfig.name,
                    },
                    {
                        label: 'data-id',
                        val: markerConfig.id,
                    }]));

                searchIds[markerConfig.name] = markerConfig.id;

                //add returned marker to category array (if cat exisits)
                if (cityLayers[markerConfig.category]) {
                    cityLayers[markerConfig.category].markersArray.push(marker);
                } else {
                    /* walter >>  this is commented out for now. Not sure if this is needed
                        cityLayers[markerConfig.category] = newCategory(
                            markerConfig.category
                        ); */
                }
            }); //end iteration

            function searchBoxInit(){

                // creates HTML structure for suggestion list
                let listWrapper = createHTMLElement('div', [
                    {
                        label: 'class',
                        val: 'query__suggestions__wrapper',
                    }
                ]);
    
                let list = createHTMLElement('ul', [
                    {
                        label: 'id',
                        val: 'query__suggestions',
                    },
                    {
                        label: 'data-show',
                        val: false,
                    },
                ]);

                searchTags.forEach(function(tag){

                    let item = createHTMLElement('li', []);

                    tag.addEventListener('click', function() {
                        searchQueryIdFind(tag.getAttribute('data-id'), tag.textContent)
                    });

                    list.appendChild(item).appendChild(tag);
                });

                searchBox.parentElement.appendChild(listWrapper).appendChild(list);

                // listens for search queries
                searchBox.addEventListener('keyup', function() {
                    let searchString = searchBox.value;
                    let list = document.getElementById('query__suggestions');
                    let find = document.getElementById('query__suggestions').querySelectorAll('li');
    
                    if(searchString.length > 0){
    
                        list.setAttribute('data-show', true);
                    
                        find.forEach(function(i){
                            let label = i.querySelector('a').textContent;
    
                            if(label.indexOf(searchString) > -1 ){
                                i.style.display = "block";
                            }
                            else {
                                i.style.display = "none";
                            }
                        });
                    }
                    else{
                        list.setAttribute('data-show', false);
                    }
                });

                function searchQueryIdFind(id, title){
                    searchBox.value = title;
                    document.getElementById('query__suggestions').setAttribute('data-show', false);
                    hashChange(id);
                }
            };

            searchBoxInit();

            document
                .getElementById('vs-button')
                .addEventListener('click', function() {
                    searchBox.focus();
                });

            //clear marker when infoWIndow closed
            google.maps.event.addListener(infoWindow, 'closeclick', function() {
                updateHash(infoWindow);
            });

            //so all markers in the buildingsArray initalially
            showOverlays(cityLayers.bigBuildingsArray);

            $mapContainer.classList.remove('loading');
            $mapContainer
                .querySelectorAll('.loading-fa-icon')
                .forEach(function(i) {
                    i.remove();
                });

            // Bind an event to window.onhashchange that, when the history state changes,
            window.addEventListener('hashchange', hashChange);

            // Since the event is only triggered when the hash changes, we need to trigger
            // the event now, to handle the hash the page may have loaded with.

            //window.trigger('hashchange'); walter below;
            hashChange(location.hash.replace('#', ''));
        }, //end parse xml,
        loadXml = function() {
    
            var xhttp = new XMLHttpRequest();

            xhttp.onreadystatechange = function() {
                if (xhttp.readyState == 4 && xhttp.status === 200) {
                    parseXml(xhttp.responseXML);
                }
            };
            xhttp.open('GET', dataSrc, true);
            xhttp.send(null);
        },
        /**
         * init fn
         * @return {Undefined}
         */
        init = function() {
            $mapContainer.classList.add('loading');
            $mapContainer.appendChild(
                createHTMLElement('i', [
                    {
                        label: 'class',
                        val: 'fa fa-refresh fa-spin loading-fa-icon',
                    },
                ])
            );

            initKmlLayers(kmlLayers);
            loadXml();

            //scroll journey planner
            document
                .getElementById('journey-link')
                .addEventListener('click', function(e) {
                    e.preventDefault();
                    /* walter >> edited out for now
                $('html, body').animate(
                    {
                        scrollTop: $('#journey-planner').offset().top,
                    },
                    1000
                );
                */
                });
        };

    init();
}

export default {
    launchFn: createMap,
    launchQuery: `.${className}`,
};
